import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { SecurityComponent } from './security.component';
import { SecuritySetupComponent } from './components/security-setup/security-setup.component';
import { SecurityActiveAlertsComponent } from './components/security-active-alerts/security-active-alerts.component';
import { SecurityHistoryComponent } from './components/security-history/security-history.component';
import { SecurityReportsComponent } from './components/security-reports/security-reports.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: SecuritySetupComponent},
	{ path: 'active-alerts', component: SecurityActiveAlertsComponent},
	{ path: 'history', component: SecurityHistoryComponent},
	{ path: 'reports', component: SecurityReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SecurityRoutingModule { }
